@import "../../inc/vars";
body.page-template-page-ppc-landing {
	background-color: #FAF8FF;
	.block-page {
		background-color: #FAF8FF;
	}
	.footer-holder {
		background-color: #fff;
	}
}
.acrobits_hero_scrolling {
	overflow-x: hidden;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	top:0;
	@media (min-width: $screen-sm) {
		flex-wrap: nowrap;
		padding-bottom: 90px;
	}
	&__content {
		padding: 40px 30px 54px;
		@media (max-width: $screen-sm) {
			transform: none !important;
		}
		@media (min-width: $screen-sm) {
			min-width: 760px;
			max-width: 760px;
			padding: 40px 77px;
		}
		&__title {
			text-align: center;
			@media (min-width: $screen-sm) {
				text-align: left;
			}
			h1 {
				@include titleGrad();
				@include pop(400, 44, 1.12);
				letter-spacing: -0.88px;
				margin-bottom: 24px;
				@media (min-width: $screen-sm) {
					@include pop(400, 53, 1.24);
					letter-spacing: -1.06px;
				}
			}
			h3 {
				@include titleGrad();
				@include fig(500, 20, 1.24);
				margin-bottom: 30px;
				display: flex;
				align-items: center;
				flex-direction: column;
				justify-content: center;
				@media (min-width: $screen-sm) {
					flex-direction: row;
					justify-content: flex-start;
					text-align: left;
				}
				img {
					margin-left: 8px;
				}
			}
		}
		&__description {
			@include fig(400, 20, 1.24);
			color: #4F4B58;
			text-align: center;
			margin-bottom: 24px;
			@media (min-width: $screen-sm) {
				text-align: left;
				margin-bottom: 48px;
			}
		}
		&__button {
			.btn {
				@include greenButton();
			}
		}
	}
	&__image {
		width: 100%;
		padding-top: 40px;
		@media (max-width: $screen-sm) {
			transform: none !important;
		}
		@media (min-width: $screen-sm) {
			min-width: 1570px;
			max-width: 1570px;
			padding: 40px 30px 0 30px;
			margin-left: -170px;
		}
		&__wrapper {
			.mac-wrapper {
				position: relative;
				display: flex;
				flex-direction: column;
				.images-wrapper {
					position: relative;
					overflow: hidden;
					order: 3;
					.mac {
						position: relative;
						z-index: 2;
						width: 144%;
						height: auto;
						left: -34%;
						@media (min-width: $screen-sm) {
							left: 0;
							width: 1022px;
						}
					}
					.circles {
						position: absolute;
						bottom: 0;
						left: 50%;
						transform: translateX(-50%);
						max-width: 100%;
						height: auto;
						@media (min-width: $screen-sm) {
							transform: translateX(-70%);
						}
					}
				}
				.feature {
					top: 0;
					z-index: 4;
					width: 220px;
					margin: 0 auto 37px auto;
					position: relative;
					padding-left: 20px;
					@media (min-width: $screen-sm) {
						width: 260px;
						position: absolute;
						margin: 0;
						padding-left: 0;
					}
					&__title {
						display: flex;
						align-items: center;
						@include pop(400, 20);
						color: #5A6272;
						margin-bottom: 16px;
						@media (min-width: $screen-sm) {
							margin-bottom: 3px;
						}
						&__image {
							position: absolute;
							@media (min-width: $screen-sm) {
								position: relative;
								margin-right: 9px;
								right: auto !important;
								left: auto !important;
							}
							&:before {
								content: '';
								display: block;
								height: 1.5px;
								background: #7048C7;
								position: absolute;
								bottom: 0;
								left: 0;
								width: 1.5px;
								@media (min-width: $screen-sm) {
									height: 1.5px !important;
								}
							}
							&:after {
								content: '';
								display: block;
								height: 9px;
								width: 9px;
								border: 1.5px #7048C7 solid;
								position: absolute;
								bottom: 0;
								left: calc(50% - 5px);
								border-radius: 50%;
							}
						}
					}
					&__content {
						@include fig(400, 15, 1.56);
						color: #4F4B58;
						@media (min-width: $screen-sm) {
							padding: 0 7px;
						}
					}
					&1 {
						order: 2;
						.feature__title__image {
							right: -70px;
							top: 0;
							&:before {
								height: 128px;
								left: calc(50% - 1px);
								top: calc(100% - 3px);
							}
							&:after {
								left: calc(50% - 5px);
								top: 171px;
							}
						}
						@media (min-width: $screen-sm) {
							top: 25px;
							right: 179px;
							.feature__title__image {
								&:before {
									width: 170px;
									right: 100%;
									top: 50%;
									left: auto;
								}
								&:after {
									top: calc(50% - 4px);
									left: -178px;
								}
							}
						}
					}
					&2 {
						order: 1;
						.feature__title__image {
							left: -40px;
							top: 0;
							&:before {
								height: 314px;
								left: calc(50% - 1px);
								top: calc(100% - 3px);
							}
							&:after {
								left: calc(50% - 5px);
								top: 357px;
							}
						}
						@media (min-width: $screen-sm) {
							right: 234px;
							top: 181px;
							left: auto;
							.feature__title__image {
								&:before {
									width: 440px;
									right: 100%;
									top: 50%;
									left: auto;
								}
								&:after {
									top: calc(50% - 4px);
									left: -448px;
								}
							}
						}
					}
					&3 {
						order: 4;
						.feature__title__image {
							left: -40px;
							top: 0;
							&:before {
								height: 161px;
								left: calc(50% - 1px);
								bottom: calc(100%);
							}
							&:after {
								left: calc(50% - 5px);
								bottom: 207px;
							}
						}
						@media (min-width: $screen-sm) {
							right: 36px;
							top: 298px;
							width: 201px;
							.feature__title__image {
								&:before {
									width: 350px;
									right: 100%;
									top: 50%;
									left: auto;
								}
								&:after {
									top: calc(50% - 4px);
									left: -358px;
								}
							}
						}
					}
					&4 {
						order: 5;
						.feature__title__image {
							right: -70px;
							top: 0;
							&:before {
								height: 300px;
								left: calc(50% - 1px);
								bottom: 100%;
							}
							&:after {
								left: calc(50% - 5px);
								bottom: 346px;
							}
						}
						@media (min-width: $screen-sm) {
							right: 179px;
							top: 385px;
							.feature__title__image {
								&:before {
									width: 170px;
									right: 100%;
									top: 50%;
									left: auto;
								}
								&:after {
									top: calc(50% - 4px);
									left: -178px;
								}
							}
						}
					}
				}
			}
		}
	}
}